<template>
  <!-- 第一种方案 -->
  <div style="height: 400px;" _class="new-scroll1">
<!--    <div  style="height: 20px;">{{this.pri_detail.communityObj.title}}</div>-->
<!--    <el-scrollbar  style="height: 350px;    max-height: 350px; ">-->

        <div class="row" >
          <div class="row_item" v-for="item in  this.builderRows ">
<!--          <div class="row_item" v-for="item in  200 ">-->
<!--            <el-tag effect="dark" @click.native.stop="showBuilderInfo(item)" >{{item.bi_build_name}} </el-tag>-->
            <el-tooltip   effect="dark" :content="item.bi_build_name" placement="top">
              <el-button  type="primary"  style="font-size:16px;padding:5px;width:100px;height: 45px;" @click.native.stop="showBuilderInfo(item)" >
<!--{{item}}-->
                {{item.bi_build_name.length>6?item.bi_build_name.substring(0,5)+'...':item.bi_build_name}}
              </el-button>
            </el-tooltip>
          </div>


        </div>

<!--    </el-scrollbar>-->
<!--    <div  style="height: 20px;">{{"共["+this.builderRows.length+"]个楼栋"}}</div>-->
  </div>

  <!-- 第二种方案 -->
<!--  <div class="row" style=" ">-->
<!--    <div class="row_item" v-for="item in  this.builderRows ">-->
<!--      <el-tag effect="dark" @click.native.stop="showBuilderInfo(item)" style="">{{item.bi_build_name}} </el-tag>-->
<!--    </div>-->


<!--  </div>-->

</template>

<script>


import Bus from "../../../utils/Bus";

export default {
  name: "CustomComponent",
  props: {

    pri_detail: {
      type: Object,
      default: function () {
        return {
          communityObj:{},


        }

      }
    }
  },
  data(){
    return{
      builderRows:[],
      // builderObj:new Map(),
    }
  },
  created() {
    this.queryBuilder();
  },
  methods:{

    showBuilderInfo(data){
        console.info("id:"+JSON.stringify(data));
       // this.builderObj.set(data.bi_uuid,data.bi_build_number);
      Bus.$emit('globalMsgForCommunityBuilder',{
        builder:data,
        community:{ci_uuid:this.pri_detail.communityObj.id,ci_community_name:this.pri_detail.communityObj.title}
      });
      this.$emit("parent_method",{
        builder:data,
        community:{ci_uuid:this.pri_detail.communityObj.id,ci_community_name:this.pri_detail.communityObj.title}
      });
    },
    queryBuilder(){
      this.builderRows = [];
      var that = this;
      let _jsondata_ =   {
        bi_community_id:this.pri_detail.communityObj.id,
      } ;
      // const loading = this.$loading({
      //   lock: true,
      //   text: '查询中...',
      //   target: '.main',
      //   spinner: 'el-icon-loading',
      //
      //   background: 'rgba(0, 0, 0, .7)'
      // });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryBuilderKV,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );

      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          // loading.close();
          this.builderRows = res.data.rows;


        }else{
          console.info("查询失败");
          // loading.close();

          that.builderRows = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        // loading.close();
        that.builderRows = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        // loading.close();

        that.builderRows = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    }
  }
}
</script>

<style scoped>
.row{
  display: flex;
  align-items: center;
  width:100%;
  flex-wrap: wrap;
  margin-left:0px;
  margin-right:10px;

}
.row_item{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width:15%;
  padding:0px;
  margin:5px;
  /*padding-top: 10px;*/
  /*padding-bottom: 10px;*/
}
.el-tag:hover{
  cursor: pointer;
}
.new-scroll1  /deep/  .el-scrollbar>.el-scrollbar__wrap {
  overflow-x: hidden !important;
  /*overflow-y: hidden !important;*/
}
</style>



